<template>
  <div :class="[{ overlay: isOpen }]" @click="onCloseMenu"></div>
  <div :class="['mobile', { 'mobile--is-open': isOpen }]">
    <div class="mobile__closeBtn" @click="onCloseMenu"></div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, watch } from 'vue/dist/vue.esm-bundler.js';
import { useRoute } from 'vue-router';
import { mapMutations } from "vuex/dist/vuex.esm-bundler.js";

export default defineComponent({
  name: 'HeaderNavMobile',
  emits: ['onCloseMenu'],
  props: {
    isOpen: {
      type: Boolean,
      required: true,
    },
  },
  methods: {
    ...mapMutations({
      showObligationsCalcModal: 'modalObligationsCalcStore/showModal',
    }),
  },
  setup(props, { emit }) {
    const route = useRoute();

    console.log(this)

    const isOpenActivesAccordion = ref<boolean>(false);
    const isOpenActivesAccordionToServiceMenu = ref<boolean>(false);

    const onCloseMenu = () => {
      emit('onCloseMenu');
    };

    const checkRoute = () => {
      if (props.isOpen) {
        onCloseMenu();
      }
    };

    watch(
      () => route.path,
      () => {
        checkRoute();
      },
    );

    return {
      onCloseMenu,
      isOpenActivesAccordion,
      isOpenActivesAccordionToServiceMenu,
    };
  },
});
</script>

